import { EPT, Button } from './utils'
import Player from './gameObjects/Player'
import Debugger from './gameObjects/Debugger'
import Goomba from './gameObjects/Goomba'
import Coin from './gameObjects/Coin'
import Flag from './gameObjects/Flag'

import tiles from './config/tiles'
import generateAnimations from './config/animations'

export default class Game extends Phaser.Scene {
	constructor() {
		super('Game');
	}





	preload() {
		 // map made with Tiled in JSON format
		 this.load.tilemapTiledJSON('map', 'assets/map.json');
		 // tiles in spritesheet 
		 this.load.spritesheet('tiles', 'assets/tiles.png', {frameWidth: 70, frameHeight: 70});
		 // simple coin image
		 this.load.image('coin', 'assets/coinGold.png');
		 // player animations
		 this.load.atlas('player', 'assets/player.png', 'assets/player.json');

	}

	create() {
		// load the map 
		this.map = this.make.tilemap({key: 'map'});
	
		// tiles for the ground layer
		this.groundTiles = this.map.addTilesetImage('tiles');
		// create the ground layer
		this.groundLayer = this.map.createDynamicLayer('World', this.groundTiles, 0, 0);
		// the player will collide with this layer
		this.groundLayer.setCollisionByExclusion([-1]);
	
		// coin image used as tileset
		this.coinTiles = this.map.addTilesetImage('coin');
		// add coins as tiles
		this.coinLayer = this.map.createDynamicLayer('Coins', this.coinTiles, 0, 0);
	
		// set the boundaries of our game world
		this.physics.world.bounds.width = this.groundLayer.width;
		this.physics.world.bounds.height = this.groundLayer.height;
	
		// create the player sprite    
		this.player = this.physics.add.sprite(200, 1780, 'player').setScale(0.2)
		this.player.setBounce(0.2); // our player will bounce from items
		this.player.setCollideWorldBounds(true); // don't go out of the map    
		
		// small fix to our player images, we resize the physics body object slightly
		this.player.body.setSize(this.player.width /2, this.player.height /2 + 60);
		
		
		// player will collide with the level tiles 
		this.physics.add.collider(this.groundLayer, this.player);
	
		this.coinLayer.setTileIndexCallback(1, this.collectCoin, this);
		// when the player overlaps with a tile with index 17, collectCoin 
		// will be called    
		this.physics.add.overlap(this.player, this.coinLayer);
	
		// player walk animation
		this.anims.create({
			key: 'walk',
			frames:  this.anims.generateFrameNames('player', {prefix: 'Run', start: 1, end: 9}),
			frameRate: 10,
			repeat: -1
		});
		// idle with only one frame, so repeat is not neaded
		// player walk animation
		this.anims.create({
			key: 'idle',
			frames: this.anims.generateFrameNames('player', {prefix: 'Idle', start: 1, end: 9}),
			frameRate: 10,
			repeat: -1
		});
	
	
		this.cursors = this.input.keyboard.createCursorKeys();
	
		// set bounds so the camera won't go outside the game world
		this.cameras.main.setBounds(0, 0, this.map.widthInPixels, this.map.heightInPixels);
		// make the camera follow the player
		this.cameras.main.startFollow(this.player);
	
		// set background color, so the sky is not black    
		this.cameras.main.setBackgroundColor('#ccccff');
	
		// this text will show the score
		this.text = this.add.text(20, 570, '0', {
			fontSize: '20px',
			fill: '#ffffff'
		});
		// fix the text to the camera
		this.text.setScrollFactor(0);
	}
	
	// this function will be called when the player touches a coin
	collectCoin(sprite, tile) {
		this.coinLayer.removeTileAt(tile.x, tile.y); // remove the tile/coin
		this.score++; // add 10 points to the score
		this.text.setText(this.score); // set the text to show the current score
		return false;
	}
	
	update(time, delta) {
		if (this.cursors.left.isDown)
		{
			this.player.body.setVelocityX(-200);
			this.player.anims.play('walk', true); // walk left
			this.player.flipX = true; // flip the sprite to the left
		}
		else if (this.cursors.right.isDown)
		{
			this.player.body.setVelocityX(200);
			this.player.anims.play('walk', true);
			this.player.flipX = false; // use the original sprite looking to the right
		} else {
			this.player.body.setVelocityX(0);
			this.player.anims.play('idle', true);
		}
		// jump 
		if (this.cursors.up.isDown && this.player.body.onFloor())
		{
			this.player.body.setVelocityY(-500);        
		}
	}

	initUI() 
	{
		this.buttonPause = new Button(EPT.world.width -100, EPT.world.height -40, 'button-pause', this.managePause, this);
		this.buttonPause.setOrigin(0, 0);

		this.buttonRestart = new Button(EPT.world.width -220, EPT.world.height -40, 'button-restart', this.stateRestart, this);
		this.buttonRestart.setOrigin(0,0).setScale(0.45);
		this.buttonRestart.y = EPT.world.height + 20;
	


		var fontScore = { font: '38px ' + EPT.text['FONT'], fill: '#ffde00', stroke: '#000', strokeThickness: 5 };
		var fontScoreWhite = { font: '38px ' + EPT.text['FONT'], fill: '#000', stroke: '#ffde00', strokeThickness: 5 };


		this.buttonPause.y = EPT.world.height + 20;
		this.tweens.add({ targets: [this.buttonPause,this.buttonRestart], y: EPT.world.height - 100, duration: 500, ease: 'Back' });

		var fontTitle = { font: '48px ' + EPT.text['FONT'], fill: '#000', stroke: '#ffde00', strokeThickness: 10 };

		this.screenPausedGroup = this.add.group();
		this.screenPausedBg = this.add.sprite(0, 0, 'overlay');
		this.screenPausedBg.setAlpha(0.95);
		this.screenPausedBg.setOrigin(0, 0);
		this.screenPausedText = this.add.text(EPT.world.centerX, 100, EPT.text['gameplay-paused'], fontTitle);
		this.screenPausedText.setOrigin(0.5, 0);
		this.screenPausedBack = new Button(100, EPT.world.height - 100, 'button-mainmenu', this.stateBack, this);
		this.screenPausedBack.setOrigin(0, 1);
		this.screenPausedContinue = new Button(EPT.world.width - 100, EPT.world.height - 100, 'button-continue', this.managePause, this);
		this.screenPausedContinue.setOrigin(1, 1);
		this.screenPausedGroup.add(this.screenPausedBg);
		this.screenPausedGroup.add(this.screenPausedText);
		this.screenPausedGroup.add(this.screenPausedBack);
		this.screenPausedGroup.add(this.screenPausedContinue);
		this.screenPausedGroup.setVisible(false);

		this.screenGameoverGroup = this.add.group();
		this.screenGameoverBg = this.add.sprite(0, 0, 'overlay');
		this.screenGameoverBg.setAlpha(0.95);
		this.screenGameoverBg.setOrigin(0, 0);
		this.screenGameoverText = this.add.text(EPT.world.centerX, 100, EPT.text['gameplay-gameover'], fontTitle);
		this.screenGameoverText.setOrigin(0.5, 0);
		this.screenGameoverBack = new Button(100, EPT.world.height - 100, 'button-mainmenu', this.stateBack, this);
		this.screenGameoverBack.setOrigin(0, 1);
		this.screenGameoverRestart = new Button(EPT.world.width - 100, EPT.world.height - 100, 'button-restart', this.stateRestart, this);
		this.screenGameoverRestart.setOrigin(1, 1);
		this.screenGameoverScore = this.add.text(EPT.world.centerX, 300, EPT.text['gameplay-score'] + this._score, fontScoreWhite);
		this.screenGameoverScore.setOrigin(0.5, 0.5);
		this.screenGameoverGroup.add(this.screenGameoverBg);
		this.screenGameoverGroup.add(this.screenGameoverText);
		this.screenGameoverGroup.add(this.screenGameoverBack);
		this.screenGameoverGroup.add(this.screenGameoverRestart);
		this.screenGameoverGroup.add(this.screenGameoverScore);
		this.screenGameoverGroup.setVisible(false);
	}
	stateRestart() {
		EPT.Sfx.play('click');
		EPT.fadeOutScene('Game', this);
	}
	stateBack() {
		EPT.Sfx.play('click');
		EPT.fadeOutScene('MainMenu', this);
	}


}